const { gql } = require('apollo-server-express');

const typeDefs = gql `
	type Owner {
		id: ID!
		name: String!
		address: String!
		phone: String
		email: String!
		pets: [Pet]
	}

	interface Pet {
		id: ID!
		name: String!
		colour: String!
		age: Int!
		breed: String
	}

	type cat implements Pet {
		id: ID!
		name: String!
		colour: String!
		age: Int!
		breed: String
	}

	type dog implements Pet {
		id: ID!
		name: String!
		colour: String!
		age: Int!
		breed: String
	}

	type Query {
		getOwners: [Owner],
		getPets: [Pet],
		getOwnerPets(owner_id: ID!): [Pet]
		
	}

	type Mutation {
		addPet(name: String!, colour: String!, age: Int!, breed: String): Pet
		updatePet(pet_id: ID!, name: String!, colour: String!, age: Int!, breed: String): Pet
	  }
`;


export default typeDefs;